import React, { useState, useEffect } from "react";
import { BsCloudUpload } from "react-icons/bs";
import axios from "axios";
import SuccessPop from "../components/SuccessPop";
import ErrorPop from "../components/ErrorPop";
export const Editnewdate = ({
  isOpen,
  onClose,
  children,
  speedDateEdit,
  refreshpage,
  speedIdedit,
}) => {
  const [selectedFile, setSelectedFile] = useState(null);
  const [spinner, setspinner] = useState(false);
  const [makeImageUse, setmakeImageUse] = useState(false);
  const [name, setName] = useState("");
  const [description, setDescription] = useState("");
  const [showPopSuccess, setShowPopSuccess] = useState(false);
  const [showPopError, setShowPopError] = useState(false);
  const [successPopMessage, setPopsuccessMessage] = useState("");
  const [errorPopMessage, setPoperrorMessage] = useState("");
  const [errorMessage, setErrorMessage] = useState("");
  const [image, setImage] = useState(null);
  const [errorMessageC, setErrorMessageC] = useState("");
  const [Userimage, setUserimage] = useState("");
  const [eventData, setEventData] = useState({
    name: "",
    description: "",
    speed_date: "",
    speed_time: "",
  });
  const [UserData, setUserData] = useState("");
  var apiUrl = "https://backend.amourette.no/api/profile/";

  const handleOverlayClick = (e) => {
    // Check if the clicked element is the overlay
    if (e.target.classList.contains("popup-overlay")) {
      onClose(); // Close the popup if the overlay is clicked
    }
  };
  useEffect(() => {
    if (speedDateEdit) {
      setEventData({
        name: speedDateEdit.name,
        description: speedDateEdit.description,
        speed_date: speedDateEdit.days ? String(speedDateEdit.days) : "",
      });
      setUserimage(speedDateEdit.image);

      setmakeImageUse(speedDateEdit.makeImageUse);
      console.log(speedDateEdit.makeImageUse);
      // Ensure that the value is correctly parsed into a Date object
    }
  }, [speedDateEdit]);

  const MAX_FILE_SIZE = 2 * 1024 * 1024; // 2 MB
  const handleFileChange = (event) => {
    const file = event.target.files[0];
    if (file) {
      // Check if the file is an image
      const isImage = file.type.startsWith("image/");
      if (!isImage) {
        setErrorMessage("Please upload a valid image file.");
        setSelectedFile(null);
        return;
      }

      // Check the file size
      if (file.size > MAX_FILE_SIZE) {
        setErrorMessage("File size must be less than 2 MB.");
        setSelectedFile(null);
        return;
      }

      // Clear any previous error messages and set the selected file
      setErrorMessage("");
      setSelectedFile(file);
    }
  };
  var apiUrll = "https://backend.amourette.no/api/";
  useEffect(() => {
    const storedUsername = localStorage.getItem("userLogin");
    const userLogin = JSON.parse(storedUsername);

    const checkuser = async () => {
      let formData = {
        id: userLogin.id,
        email: userLogin.email,
      };

      try {
        const res = await axios.post(apiUrll + "getUserDetail", formData, {
          headers: {
            Accept: "application/json",
            "Content-Type": "application/json", // Ensure the content type is JSON
          },
        });

        setUserData(res.data.result);
      } catch (err) {
        // Enhanced error handling
        if (err.response) {
        } else if (err.request) {
          console.error("Request data:", err.request);
        } else {
          console.error("Error message:", err.message);
        }
      }
    };
    checkuser();
  }, [speedDateEdit]);
  const handleSubmit = async (event) => {
    event.preventDefault();
    setspinner(true);
    const storedUsername = localStorage.getItem("userLogin");
    const userLogin = storedUsername ? JSON.parse(storedUsername) : null;
    try {
      const formData = new FormData();
      //console.log("eventData:", eventData);

      for (const key in eventData) {
        if (eventData[key] !== undefined && eventData[key] !== null) {
          formData.append(key, eventData[key]);
        }
      }

      formData.append("speedIdedit", speedIdedit);
      formData.append("makeImageUse", makeImageUse ? 1 : 0);
      formData.append("user_id", userLogin.id);
      formData.append("email", userLogin.email);
      formData.append("fileType", "speeddate");
      if (makeImageUse === 1) {
        formData.append("image", UserData.profile_image);
      } else {
        if (selectedFile === null) {
          formData.append("image", Userimage);
        } else {
          formData.append("image", selectedFile);
        }
      }

      if (selectedFile !== null) {
        handleSubmitvalue();
      } else {
        const response = await axios.post(
          apiUrl + "speeddateUpdatess",
          formData,
          {
            headers: {
              Accept: "application/json",
              "Content-Type": "application/json",
            },
          }
        );
        refreshpage();
        setspinner(false);
        setShowPopSuccess(true);
        setPopsuccessMessage(response.data.message);
        setTimeout(() => {
          setShowPopError(false);
          setShowPopSuccess(false);
          window.location.reload();
          onClose();
        }, 800);
        console.log("Event created:", response.data);
        setImage(null);
        setName("");
        setDescription("");
        setmakeImageUse(false);
        setSelectedFile(null);
      }
    } catch (error) {
      setspinner(false);
      setShowPopError(true);
      setTimeout(() => {
        setShowPopError(false);
        onClose();
      }, 800);
      setPoperrorMessage("Error creating esssvent:", error);
    }
  };
  const handleSubmitvalue = async () => {
    setspinner(true);
    const storedUsername = localStorage.getItem("userLogin");
    const userLogin = storedUsername ? JSON.parse(storedUsername) : null;
    try {
      const formData = new FormData();

      for (const key in eventData) {
        if (eventData[key] !== undefined && eventData[key] !== null) {
          formData.append(key, eventData[key]);
        }
      }

      formData.append("speedIdedit", speedIdedit);
      formData.append("makeImageUse", makeImageUse ? 1 : 0);
      formData.append("user_id", userLogin.id);
      formData.append("fileType", "speeddate");
      formData.append("email", userLogin.email);
      formData.append("image", selectedFile);

      const response = await axios.post(
        apiUrl + "updatespeed_dates",
        formData,
        {
          headers: {
            "Content-Type": "multipart/form-data",
          },
        }
      );
      refreshpage();
      setspinner(false);
      setShowPopSuccess(true);
      setPopsuccessMessage(response.data.message);
      setTimeout(() => {
        setShowPopError(false);
        setShowPopSuccess(false);
        window.location.reload();
        onClose();
      }, 800);
      console.log("Event created:", response.data);
      setImage(null);
      setName("");
      setDescription("");
      setmakeImageUse(false);
      setSelectedFile(null);
    } catch (error) {
      setspinner(false);
      setShowPopError(true);
      setTimeout(() => {
        setShowPopError(false);
        onClose();
      }, 800);
      setPoperrorMessage("Error creating esssvent:", error);
    }
  };
  const handleInputChange = (e) => {
    const { name, value } = e.target;
    setEventData((prevData) => ({
      ...prevData,
      [name]: value,
    }));
    if (name === "speed_date") {
      // Ensure that the value is correctly parsed into a Date object
      const selectedDate = new Date(value);
      const currentDate = new Date();

      // Ensure both dates are set to midnight to avoid time-related issues
      currentDate.setHours(0, 0, 0, 0);
      selectedDate.setHours(0, 0, 0, 0);

      // Calculate the difference in time (milliseconds)
      const diffTime = selectedDate - currentDate;

      // Convert the difference into days
      const diffDays = diffTime / (1000 * 3600 * 24);

      // Check if the selected date is more than 7 days from today
      const diffDayss = diffDays + 1;
      console.log(diffDayss);
      if (diffDayss > 7) {
        setErrorMessageC("This post cannot be more than 7 days from today.");
      } else {
        setErrorMessageC(""); // Clear the error message if the date is valid
      }
    }
  };
  // useEffect(() => {
  //   if (makeImageUse === true) {
  //   }
  //   console.log(makeImageUse);
  // }, [makeImageUse]);
  const setmakeImageUsechoose = (e) => {
    const value = e ? 1 : 0;
    setmakeImageUse(value);
    if (value === 1) {
      setUserimage(UserData.profile_image);
    } else {
      setUserimage("");
    }
    setSelectedFile(null);
  };
  const imageToDisplay = selectedFile
    ? URL.createObjectURL(selectedFile) // If a file is selected, show it
    : Userimage;

  if (!isOpen) return null;
  return (
    <>
      <div className="popup-overlay" onClick={handleOverlayClick}>
        {showPopSuccess && <SuccessPop message={successPopMessage} />}
        {showPopError && <ErrorPop message={errorPopMessage} />}
        <div className="popup-content col-md-5">
          <button className="close-button" onClick={onClose}>
            &times; {/* Close button */}
          </button>
          <div className="gallery_pop  d-flex flex-column gap-4">
            <h3>Edit date</h3>
            <form action="javascript:void(0)" onSubmit={handleSubmit}>
              <div className="row gy-3 align-items-end">
                <div className="col-md-6">
                  <div className="uploadfile rel bgload">
                    <input
                      type="file"
                      required={!Userimage}
                      name="image"
                      onChange={handleFileChange}
                      className="fileload"
                      disabled={makeImageUse} // Disable file input if "Use Profile Picture" is checked
                    />
                    <div className="upload_image">
                      {makeImageUse && selectedFile === null && (
                        <img
                          className="w-100 h-100 s"
                          src={Userimage} // Replace with actual URL of the profile picture
                          alt="Profile"
                        />
                      )}

                      {selectedFile && (
                        <img
                          className="w-100 h-100"
                          src={imageToDisplay} // Create a URL for the selected image
                          alt="Selected"
                        />
                      )}
                      {selectedFile === null &&
                        makeImageUse === 0 &&
                        Userimage !== "" && (
                          <img
                            className="w-100 h-100 s"
                            src={Userimage} // Create a URL for the selected image
                            alt="Profile"
                          />
                        )}
                    </div>
                    <div className="inload" style={{ height: "100px" }}>
                      <div>
                        <BsCloudUpload style={{ fontSize: "24px" }} />
                        <h3 style={{ fontSize: "14px" }}>Upload image *</h3>
                      </div>
                    </div>
                    {errorMessage && (
                      <p className="text-danger">{errorMessage}</p>
                    )}
                  </div>
                </div>
                <div className="col-md-6">
                  <div className="listbox">
                    <label className="cbox">
                      Use profile picture
                      <input
                        type="checkbox"
                        checked={makeImageUse}
                        onChange={(e) =>
                          setmakeImageUsechoose(e.target.checked)
                        }
                        name="makeImagePrivate"
                      />
                      <span className="checkmark"></span>
                    </label>
                  </div>
                </div>
                <div className="col-12">
                  <input
                    type="text"
                    defaultValue={eventData.name}
                    required
                    onChange={(e) =>
                      setEventData((prev) => ({
                        ...prev,
                        name: e.target.value,
                      }))
                    }
                    placeholder="Date name *"
                  />
                </div>
                <div className="col-12">
                  <textarea
                    rows="8"
                    defaultValue={eventData.description}
                    onChange={(e) =>
                      setEventData((prev) => ({
                        ...prev,
                        description: e.target.value,
                      }))
                    }
                    placeholder="Date description"
                  ></textarea>
                </div>
                <div className="col-md-12">
                  <label className="stextlabel">Choose Days * </label>

                  <select
                    value={eventData.speed_date || ""}
                    onChange={handleInputChange}
                    name="speed_date"
                    required
                  >
                    <option value="">--Select--</option>
                    <option value="1">1 day</option>
                    <option value="2">2 days</option>
                    <option value="3">3 days</option>
                    <option value="4">4 days</option>
                    <option value="5">5 days</option>
                    <option value="6">6 days</option>
                    <option value="7">7 days</option>
                  </select>
                </div>
                <span className="text-danger">{errorMessageC}</span>
                <div className="col-12">
                  {spinner && (
                    <div
                      className="spinner-border text-success date-load"
                      role="status"
                    >
                      <span className="visually-hidden">Loading...</span>
                    </div>
                  )}
                  <button type="submit">Update</button>
                </div>
              </div>
            </form>
          </div>
        </div>
      </div>
    </>
  );
};
